import { FC, FormEvent, useState } from "react";
import { DELAY_IN_MS } from "../../constants/delays";
import { delay } from "../../utils/utils";
import { Button } from "../ui/button/button";
import { Circle } from "../ui/circle/circle";
import { Input } from "../ui/input/input";
import { SolutionLayout } from "../ui/solution-layout/solution-layout";
import style from './string.module.css'
import { flipIndex, MAXLEN, paintingCircle } from "./utils";

export const StringComponent: FC = () => {
  const [inputValue, setInputValue] = useState('');             // 
  const [isLoader, setIsLoader] = useState(false);              // 
  const [nextIndex, setNextIndex] = useState(0);                //  
  const [reverseArray, setReverseArray] = useState<Array<string>>([]);  //   

  const onChange = (e: FormEvent<HTMLInputElement>) => {          //   
    const string = e.currentTarget.value;
    setInputValue(string);
  }

  const reverseString = async (string: string) => {
    const array = string.split('');          // 
    setReverseArray([...array]);             //   
    setNextIndex(0);                         //   0
    setIsLoader(true);                       //  
    await delay(DELAY_IN_MS);                // 

    let left = 0;                            //    -
    let right = array.length - 1;            //   -
    while (left <= right) {
      flipIndex(array, left, right);                             //  
      left++;                                                    //  
      right--;                                                   // 
      setNextIndex(left => left + 1);                            //  
      setReverseArray([...array]);                               //   
      await delay(DELAY_IN_MS);                                  //  
    }

    setIsLoader(false);
    return array;
  }

  const onClick = (e: FormEvent<HTMLFormElement> | FormEvent<HTMLButtonElement>) => {
    e.preventDefault();
    reverseString(inputValue); //  
    setInputValue('');         // 
  }

  return (
    <SolutionLayout title="">
      <form className={style.form} onSubmit={onClick} data-cy="form">
        <Input
          onChange={onChange}
          isLimitText={true}
          maxLength={MAXLEN}
          value={inputValue}
          extraClass="mr-6"
          data-cy="input" />
        <Button
          text=""
          isLoader={isLoader}
          onClick={onClick}
          disabled={!inputValue}
          data-cy="button"
        />
      </form>
      <ul className={style.list}>
        {reverseArray.map((letter: string, index: number) => {
          return (
            <Circle
              key={index}
              letter={letter}
              state={paintingCircle(index, nextIndex, reverseArray)}
            />)
        })}
      </ul>
    </SolutionLayout>
  );
};